.story-section {
	position: relative;
	background: var(--color-primary);
	color: var(--color-light);
	.orn-1 {
		position: relative;
		width: 180px;
		margin: 0 auto;
	}
	.orn-2 {
		position: absolute;
		top: 0;
		left: 0;
		width: 240px;
		transform: translate(-55%, 0) scaleX(-1);
		@include max-lg {
			width: 220px;
		}
		@include max-md {
			width: 180px;
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 80% 100%;
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		right: 0;
		width: 240px;
		transform: translate(55%, 0);
		@include max-lg {
			width: 220px;
		}
		@include max-md {
			width: 180px;
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 80% 100%;
		}
	}
	.story {
		position: relative;
		padding: 5rem 0;
		z-index: 3;
		@include max-md {
			padding: 3rem 0;
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		text-align: center;
		margin-bottom: 2rem;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 3/4;
		padding: 1rem;
		border: 2px solid var(--color-main);
		border-radius: 4px;
		@include max-md {
			padding: 0.75rem;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.story-background {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 100%;
				border-radius: 4px;
			}
		}
	}
}
