.thank-section {
	position: relative;
	.orn-1 {
		position: relative;
		width: 100%;
		margin-top: -2px;
		@include max-lg {
			width: 150%;
		}
		@include max-md {
			width: 200%;
		}
	}
	.orn-2 {
		position: relative;
		width: 240px;
		margin: 0 auto;
		@include max-md {
			width: 180px;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: -2px;
		left: -2px;
		width: 23%;
		@include max-lg {
			width: 28%;
		}
		@include max-md {
			width: 35%;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: -2px;
		right: -2px;
		width: 23%;
		@include max-lg {
			width: 28%;
		}
		@include max-md {
			width: 35%;
		}
	}
	.orn-5 {
		position: absolute;
		top: 0;
		left: 0;
		width: 280px;
		transform: translate(-40%, -50%);
		@include max-lg {
			width: 260px;
		}
		@include max-md {
			width: 180px;
			transform: translate(-50%, -50%);
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 20% 100%;
		}
		.orn-6 {
			position: absolute;
			top: 0;
			left: 0;
			width: 200px;
			transform: translate(10%, -80%);
			@include max-lg {
				width: 180px;
			}
			@include max-md {
				width: 140px;
				transform: translate(20%, -80%);
			}
			@include max-xs {
				width: 120px;
			}
		}
	}
	.orn-7 {
		position: absolute;
		top: 0;
		right: 0;
		width: 280px;
		transform: translate(40%, -60%);
		@include max-lg {
			width: 260px;
		}
		@include max-md {
			width: 180px;
			transform: translate(50%, -60%);
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 80% 100%;
		}
		.orn-8 {
			position: absolute;
			top: 0;
			left: 0;
			width: 200px;
			transform: translate(20%, -100%) scaleX(-1);
			@include max-lg {
				width: 180px;
			}
			@include max-md {
				width: 140px;
			}
			@include max-xs {
				width: 120px;
			}
			img {
				transform-origin: 20% 100%;
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 90%;
		height: auto;
		aspect-ratio: 3/4;
		margin: 0 auto;
		padding: 1rem;
		border: 2px solid var(--color-primary);
		border-radius: 4px;
		@include max-md {
			padding: 0.75rem;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 4px;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		padding: 5rem 0;
		text-align: center;
		@include max-md {
			padding: 3rem 0;
		}
		.thank {
			position: relative;
			padding: 3rem 2rem;
			@include max-md {
				padding: 2rem 1rem 5rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
