.header-section {
	position: relative;
	min-height: 100vh;
	width: 100%;
	@include max-lg {
		height: 100vh;
	}
	.scroll-icon {
		position: relative;
		margin-top: 2rem;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		@include max-lg {
			width: 150%;
		}
		@include max-md {
			width: 200%;
		}
	}
	.orn-2 {
		position: relative;
		display: block;
		width: 240px;
		height: auto;
		margin: 0 auto;
		@include max-md {
			width: 180px;
		}
		@include max-xs {
			width: 160px;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: -2px;
		left: -2px;
		width: 23%;
		@include max-lg {
			width: 35%;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: -2px;
		right: -2px;
		width: 23%;
		@include max-lg {
			width: 35%;
		}
	}
	.orn-5 {
		position: absolute;
		top: 0;
		left: 0;
		width: 240px;
		transform: translate(-40%, -50%);
		@include max-lg {
			width: 220px;
		}
		@include max-md {
			width: 180px;
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 20% 100%;
		}
	}
	.orn-6 {
		position: absolute;
		top: 0;
		left: 0;
		width: 220px;
		transform: translate(0, -80%);
		@include max-lg {
			width: 200px;
		}
		@include max-md {
			width: 160px;
		}
		@include max-xs {
			width: 140px;
		}
	}
	.orn-7 {
		position: absolute;
		top: 0;
		right: 0;
		width: 220px;
		transform: translate(40%, -60%) scaleX(-1);
		@include max-lg {
			width: 200px;
		}
		@include max-md {
			width: 160px;
		}
		@include max-xs {
			width: 140px;
		}
		img {
			transform-origin: 20% 100%;
		}
		.orn-8 {
			position: absolute;
			top: 0;
			left: 0;
			width: 240px;
			transform: translate(0, -100%) scaleX(-1);
			@include max-lg {
				width: 220px;
				transform: translate(-20%, -100%) scaleX(-1);
			}
			@include max-md {
				width: 200px;
			}
			@include max-xs {
				width: 180px;
			}
			img {
				transform-origin: 80% 100%;
			}
		}
	}
	.header-content {
		position: relative;
		height: 100%;
		padding: 4.5rem 0 0;
		@include max-lg {
			padding: 6rem 0 0;
		}
		@include max-md {
			padding: 5rem 0 0;
		}
		.header {
			position: relative;
			width: 40%;
			height: 100%;
			margin: 0 auto;
			border: 2px solid var(--color-primary);
			border-radius: 4px;
			border-bottom: none;
			padding: 2rem;
			@include max-xl {
				width: 50%;
			}
			@include max-lg {
				width: 70%;
			}
			@include max-md {
				padding: 1rem;
				width: 100%;
			}
			@include max-xs {
				padding: 0.75rem;
			}
			.header-title {
				position: absolute;
				width: 100%;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				text-align: center;
				&-content {
					position: relative;
					border-radius: var(--border-radius);
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
			.image-wrapper {
				position: relative;
				width: 100%;
				height: auto;
				aspect-ratio: 3/4;
				margin: 0 auto;
				align-self: flex-end;
				.image-element {
					width: 100%;
					height: 100%;
					position: relative;
					&::before {
						position: absolute;
						top: 0;
						left: 0;
						right: 0;
						bottom: 0;
						background: rgba($color: #000000, $alpha: 0.3);
						content: "";
					}
					.header-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
						border-radius: 4px;
					}
				}
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}
