.rsvp-section {
	position: relative;
	background: var(--color-primary);
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: relative;
		display: block;
		width: 240px;
		margin: 0 auto;
		@include max-md {
			width: 180px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 80%;
		transform: translate(20%, -5%) scaleX(-1);
		img {
			transform-origin: 80% 100%;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: -1px;
		left: 0;
		width: 180px;
		@include max-md {
			width: 140px;
			transform: translateX(-20%);
		}
		@include max-xs {
			width: 120px;
		}
		path {
			fill: var(--color-main);
		}
	}
	.orn-4 {
		position: absolute;
		bottom: -1px;
		right: 0;
		width: 180px;
		transform: scaleX(-1);
		@include max-md {
			width: 140px;
			transform: translateX(20%) scaleX(-1);
		}
		@include max-xs {
			width: 120px;
		}
		path {
			fill: var(--color-main);
		}
	}
	.orn-5 {
		position: relative;
		width: 100%;
		margin-top: -2px;
		@include max-lg {
			width: 150%;
		}
		@include max-md {
			width: 200%;
		}
		path {
			fill: var(--color-main);
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 3/4;
		.image-element.photo {
			position: relative;
			width: 100%;
			height: 100%;
			border: 2px solid var(--color-main);
			padding: 1rem;
			border-radius: 4px;
			@include max-md {
				padding: 0.75rem;
			}
			.rsvp-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 4px;
			}
		}
	}
	.rsvp-wrapper {
		position: relative;
		margin-top: 2rem;
		.rsvp-form {
			position: relative;
		}
	}
}
