.stream-section {
	position: relative;
	padding: 5rem 0;
	background: var(--color-primary);
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 140px;
		transform: translate(-50%, 0);
		opacity: 0.6;
		@include max-lg {
			width: 120px;
		}
		@include max-md {
			width: 100px;
		}
		@include max-xs {
			width: 80px;
		}
		img {
			transform-origin: 20% 100%;
		}
		.orn-2 {
			position: relative;
			width: 160px;
			transform: translate(-30%, -40%);
			@include max-lg {
				width: 140px;
			}
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 100px;
			}
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 140px;
		transform: translate(50%, 30%) scaleX(-1);
		opacity: 0.6;
		@include max-lg {
			width: 120px;
		}
		@include max-md {
			width: 100px;
		}
		@include max-xs {
			width: 80px;
		}
		img {
			transform-origin: 20% 100%;
		}
		.orn-4 {
			position: relative;
			width: 160px;
			transform: translate(-30%, -40%);
			@include max-lg {
				width: 140px;
			}
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 100px;
			}
		}
	}
	.stream-wrapper {
		position: relative;
		z-index: 5;
		.stream-content {
			position: relative;
			.stream-description {
				position: relative;
				padding: 3rem 2rem;
				background: var(--color-main);
				border-radius: var(--border-radius);
				border-bottom-left-radius: 0;
				border-bottom-right-radius: 0;
				overflow: hidden;
				@include max-md {
					padding: 2rem 1rem;
				}
			}
			h4 {
				@include max-md {
					text-align: center;
				}
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.card {
		background-color: transparent;
		height: 100%;
		color: var(--color-dark);
		border: none;
	}
	.btn-custom {
		width: 100%;
		z-index: 10;
		position: relative;
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		width: 100%;
		margin-top: 3rem;
		@include max-md {
			text-align: center;
		}
	}
}
