.couple-section {
	position: relative;
	text-align: center;
	background: var(--color-primary);
	color: $color-custom;
	.and {
		@include min-md {
			position: absolute;
			top: 56%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		margin: 2rem auto 1rem;
		z-index: 3;
		span {
			position: relative;
			font-size: 32px;
			font-family: $font-secondary;
		}
	}
	.couple-wrapper {
		position: relative;
		padding: 5rem 0;
		z-index: 3;
		@include max-md {
			padding: 5rem 0 3rem;
		}
		.orn-1 {
			position: absolute;
			top: 0;
			left: 0;
			width: 200px;
			transform: translate(-35%, 0);
			@include max-lg {
				width: 180px;
			}
			@include max-md {
				width: 140px;
				transform: translate(-45%, 0);
			}
			@include max-xs {
				width: 120px;
			}
			img {
				transform-origin: 20% 100%;
			}
			.orn-2 {
				position: relative;
				width: 240px;
				transform: translate(-30%, -40%) scaleX(-1);
				@include max-lg {
					width: 220px;
				}
				@include max-md {
					width: 180px;
					transform: translate(-40%, -40%) scaleX(-1);
				}
				@include max-xs {
					width: 160px;
				}
				img {
					transform-origin: 80% 100%;
				}
			}
		}
		.orn-3 {
			position: absolute;
			top: 0;
			right: 0;
			width: 200px;
			transform: translate(35%, 0) scaleX(-1);
			@include max-lg {
				width: 180px;
			}
			@include max-md {
				width: 120px;
				transform: translate(45%, 0) scaleX(-1);
			}
			@include max-xs {
				width: 100px;
			}
			img {
				transform-origin: 20% 100%;
			}
			.orn-4 {
				position: relative;
				width: 240px;
				transform: translate(-30%, -40%) scaleX(-1);
				@include max-lg {
					width: 220px;
				}
				@include max-md {
					width: 180px;
					transform: translate(-40%, -40%) scaleX(-1);
				}
				@include max-xs {
					width: 160px;
				}
				img {
					transform-origin: 80% 100%;
				}
			}
		}
		.orn-5 {
			position: absolute;
			bottom: 1rem;
			left: 0;
			width: 140px;
			transform: translate(-40%, 0) scaleX(-1);
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 100px;
			}
			img {
				transform-origin: 20% 100%;
			}
		}
		.orn-6 {
			position: absolute;
			top: 0;
			right: 0;
			width: 200px;
			transform: translate(30%, 0) scaleX(-1);
			@include max-lg {
				width: 180px;
			}
			@include max-md {
				width: 160px;
			}
			img {
				transform-origin: 80% 100%;
			}
			.orn-7 {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 140px;
				transform: translate(0, 50%) scaleX(-1);
				@include max-md {
					width: 120px;
				}
				@include max-xs {
					width: 100px;
				}
				img {
					transform-origin: 20% 100%;
				}
			}
		}
		.orn-8 {
			position: absolute;
			top: 50%;
			right: 0;
			width: 200px;
			transform: translate(30%, -50%) scaleX(-1);
			@include max-lg {
				width: 180px;
			}
			img {
				transform-origin: 80% 100%;
			}
		}
		.orn-9 {
			position: absolute;
			top: 50%;
			left: 0;
			width: 140px;
			transform: translate(-40%, -35%) scaleX(-1);
			@include max-md {
				width: 120px;
			}
			img {
				transform-origin: 20% 100%;
			}
			.orn-10 {
				position: relative;
				width: 180px;
				transform: translate(-20%, -40%) scaleX(-1);
				@include max-lg {
					width: 160px;
				}
				@include max-md {
					width: 140px;
				}
				img {
					transform-origin: 80% 100%;
				}
			}
		}
		.orn-11 {
			position: absolute;
			top: 50%;
			right: 0;
			width: 6rem;
			transform: translate(98%, -50%);
			@include min-xxl {
				width: 8rem;
			}
			@include max-lg {
				display: none;
			}
			@include max-md {
				&.sm {
					display: block;
					transform: translate(2rem, -50%);
				}
			}
		}
		.orn-12 {
			position: absolute;
			top: 50%;
			left: 0;
			width: 6rem;
			transform: translate(-98%, -50%);
			@include min-xxl {
				width: 8rem;
			}
			@include max-lg {
				display: none;
			}
			@include max-md {
				&.sm {
					display: block;
					transform: translate(-2rem, -50%);
				}
			}
		}
	}
	.couple-body {
		position: relative;
	}
	.couple-content {
		position: relative;
	}
	.couple {
		position: relative;
		text-align: center;
		.image-wrap {
			width: 80%;
			position: relative;
			aspect-ratio: 7/8;
			margin: 0 auto;
			.image-element.photo {
				position: relative;
				width: 100%;
				height: 100%;
				padding: 1rem;
				border: 2px solid var(--color-main);
				border-radius: var(--border-radius);
				@include max-lg {
					padding: 0.5rem;
				}
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					border-radius: 4px;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 1rem;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			background: var(--color-light);
			padding: 0.3rem 0.4rem;
			border-radius: 50px;
			margin: 1rem auto 0;
			.sosmed {
				color: var(--color-main);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
