.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	height: 100vh;
	width: 100%;
	color: var(--color-light);
	&.cover-opened {
		transform: translateY(-100%);
	}
	.orn-1 {
		position: absolute;
		top: -1px;
		left: 0;
		width: 100%;
		@include max-lg {
			width: 150%;
		}
		@include max-md {
			width: 200%;
		}
	}
	.orn-2 {
		position: relative;
		display: block;
		width: 200px;
		height: auto;
		@include max-md {
			width: 180px;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 240px;
		transform: translate(-40%, 10%);
		@include max-lg {
			width: 220px;
		}
		@include max-md {
			width: 160px;
		}
		@include max-xs {
			width: 140px;
		}
		img {
			transform-origin: 20% 100%;
		}
		.orn-4 {
			position: absolute;
			top: 0;
			left: 0;
			width: 220px;
			transform: translate(0, -100%);
			@include max-lg {
				width: 200px;
			}
			@include max-md {
				width: 140px;
			}
			@include max-xs {
				width: 120px;
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 220px;
		transform: translate(40%, 20%) scaleX(-1);
		@include max-lg {
			width: 200px;
		}
		@include max-md {
			width: 140px;
		}
		@include max-xs {
			width: 120px;
		}
		img {
			transform-origin: 20% 100%;
		}
		.orn-6 {
			position: absolute;
			top: 0;
			left: 0;
			width: 240px;
			transform: translate(0, -110%) scaleX(-1);
			@include max-lg {
				width: 220px;
				transform: translate(-20%, -120%) scaleX(-1);
			}
			@include max-md {
				width: 180px;
			}
			@include max-xs {
				width: 160px;
			}
			img {
				transform-origin: 80% 100%;
			}
		}
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		transition: opacity 1s ease;
		@include max-lg {
			padding: 4rem 0 6rem;
			align-items: center;
		}
		.cover-content {
			text-align: center;
			position: relative;
			height: 100%;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			.cover-body {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				padding: 2rem;
				border: 2px solid var(--color-primary);
				border-radius: 4px;
				@include max-lg {
					flex-direction: column;
					width: fit-content;
					max-width: 100%;
				}
				@include max-md {
					padding: 1rem;
					transform: scale(0.8);
				}
				.greeting-wrapper {
					position: relative;
					@include max-md {
						font-size: 14px;
					}
					h6 {
						font-weight: 700;
					}
				}
				h6 {
					font-weight: 600;
				}
				.image-wrapper {
					position: relative;
					width: 240px;
					height: auto;
					aspect-ratio: 3/4;
					border-radius: 4px;
					@include max-lg {
						width: 50%;
						margin: 0 auto;
					}
					@include max-md {
						width: 100%;
						aspect-ratio: 7/8;
					}
					.cover-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
						border-radius: 4px;
						position: relative;
					}
				}
				.cover-description {
					padding: 0 2rem;
					text-align: center;
					@include max-lg {
						padding: 2rem 0 1rem;
					}
					* {
						position: relative;
					}
				}
			}
			.elements-widget {
				width: 100%;
			}
		}
	}
}
