.reminder-wrap {
	position: relative;
	display: block;
	background: var(--color-main);
	.orn-1 {
		position: relative;
		width: 100%;
		height: auto;
		@include max-md {
			width: 200%;
		}
	}
	.orn-2 {
		position: relative;
		width: 30%;
		height: auto;
		margin: 1rem auto 0;
		@include max-lg {
			width: 40%;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: -2px;
		left: -2px;
		width: 23%;
		@include max-lg {
			width: 28%;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: -2px;
		right: -2px;
		width: 23%;
		@include max-lg {
			width: 28%;
		}
	}
	.orn-5 {
		position: absolute;
		top: 0;
		left: 0;
		width: 180px;
		transform: translate(calc(-100% - 5rem), 0);
		@include max-lg {
			width: 140px;
			transform: translate(calc(-100% - 3rem), 0);
		}
		@include max-md {
			width: 70px;
			transform: translate(calc(-100% - 2rem), 0);
		}
	}
	.orn-6 {
		position: absolute;
		top: 0;
		right: 0;
		width: 180px;
		transform: translate(calc(100% + 5rem), 0);
		@include max-lg {
			width: 140px;
			transform: translate(calc(100% + 3rem), 0);
		}
		@include max-md {
			width: 70px;
			transform: translate(calc(100% + 2rem), 0);
		}
	}
	.reminder-content {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -40%);
		width: fit-content;
		max-width: 100%;
		z-index: 3;
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1.5rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
